import React from 'react';
import Link from 'gatsby-link';

const IndexPage = () => React.createElement(
  'div',
  null,
  React.createElement(
    'h1',
    null,
    'Hi people'
  ),
  React.createElement(
    'p',
    null,
    'Welcome to your new Gatsby site.'
  ),
  React.createElement(
    'p',
    null,
    'Now go build something great.'
  ),
  React.createElement(
    Link,
    { to: '/page-2/' },
    'Go to page 2'
  )
);

export default IndexPage;


